<template>
  <div class="index">
    <div class="banner">
      <h2>{{$t('message.technology.txt0')}}</h2>
    </div>
    <main>
      <div class="toMain">{{$t('message.technology.txt1')}}</div>
      <div class="content">
        <div class="right">
           <video-player
              class="video-player vjs-custom-skin"
              ref="videoPlayer"
              :playsinline="true"
              :options="playerOptions"
            ></video-player>
        </div>
        <div class="left">
          <div class="leftSize">{{$t('message.technology.txt2')}}</div>
          <div class="leftCont">{{$t('message.technology.txt3')}}</div>
          <div class="leftSizes">{{$t('message.technology.txt4')}}</div>
          <div class="leftCont">{{$t('message.technology.txt5')}}</div>
          <div class="leftCont">{{$t('message.technology.txt6')}}</div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import Header from "../components/headerApp.vue";
export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
     playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], //
        autoplay: false, //true,
        muted: false, // 
        loop: false, // 
        preload: 'auto', // <video>auto,
        language: 'zh-CN',
        aspectRatio: '16:9', //  - "16:9""4:3"
        fluid: true, // trueVideo.js player
        sources: [
          {
            type: '',
            src: '' //url
          }
        ],
        poster: '../../static/images/test.jpg', //
        // width: document.documentElement.clientWidth,
        notSupportedMessage: '', //Video.js
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true //
        }
      }
    };
  },
  components: {
    Header
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.banner {
  background: url("../assets/header.png") no-repeat;
  background-size: cover;
  h2 {
    font-size: 60px;
    font-family: ProximaNova-Regular;
    font-weight: 400;
    color: rgba(254, 254, 254, 1);
    text-align: center;
    margin: 0;
  }
}
main {
  margin-top: 134px;
  .toMain {
    font-size: 28px;
    font-family: Bodoni-Normal-Italic;
    font-weight: 400;
    color: rgba(68, 68, 68, 1);
  }
  .content {
    margin-top: 70px;
    display: flex;
    .right {
      width: 685px;
      height: 445px;
      .video-player {
        margin-top: 48px;
        width: 480px;
        height: 312px;
         background:url('../assets/vedio.png') no-repeat;
        background-size: cover;
      } 
      .video-js .vjs-big-play-button {
      }
    }
    .left {
      margin-top: 26px;
      margin-left: 35px;
      flex: 1;
      .leftSize {
        width: 100%;
        font-size: 28px;
        font-family: Bodoni-Normal-Italic;
        font-weight: 400;
        color: rgba(68, 68, 68, 1);
      }
      .leftCont {
        width: 100%;
        margin-top: 42px;
        margin-bottom: 16px;
        font-size: 16px;
        font-family: ProximaNova-Light;
        font-weight: 300;
        color: rgba(68, 68, 68, 1);
        line-height: 24px;
      }
      .leftSizes {
        width: 100%;
        font-size: 28px;
        font-family: Bodoni-Normal-Italic;
        font-weight: 400;
        color: rgba(68, 68, 68, 1);
      }
    }
  }
}
</style>




